<template>
  <div v-if="!loggedin" class="home">
    <!-- <img alt="Vue logo" src="../assets/logo.png"> -->
    <div class="header">
      <div class="title">OnePeer!</div>
    </div>
    <div @submit.prevent="handleLogin" class="form">
      <div v-if="invalid">
        <h3 style="color:red; margin-top: -5px; margin-bottom: 30px; padding-left: 20px; padding-right: 20px; text-align: left;">Incorrect username or password, try again.</h3>
      </div>
      <form>
        <input v-model="username" class="inp" type="text" placeholder="Username">
        <input v-model="password" class="inp" type="password" placeholder="Password">
        <input v-if="!loading" class="inp-btn" type="submit" value="Login">
      </form>
    </div>
  </div>
  <Chat v-else />
</template>

<script>
// @ is an alias to /src
import Chat from '@/components/Chat.vue'
import { request } from '@/utils/request'
import qs from 'qs'
import eventBus from '@/eventBus'

export default {
  name: 'home',
  data () {
    return {
      username: '',
      password: '',
      loggedin: false,
      loading: false,
      invalid: false,
    }
  },
  components: {
    Chat
  },
  mounted () {
    eventBus.$on('LOGOUT', () => {
      this.loggedin = false
    })
  },
  methods: {
    handleLogin () {
      this.invalid = false
      this.loading = true
      request('post', '?path=login', qs.stringify({username: this.username, password: this.password}))
        .then (res => {
          this.loading = false
          if(res.data.success === true) {
            localStorage.setItem('token', res.data.id)
            this.loggedin = true
          } else {
            this.invalid = true
          }
        })
        .catch(err => {
          this.loading = false
          this.invalid = true
          console.log(err)
        })
    }
  }
}
</script>

<style scoped>
  .header {
    height: 250px;
    background-image: url('../assets/logo.png');
    background-size: cover;
  }
  .header .title {
    font-size: 30px;
    font-weight: bold;
    color: rgb(205, 255, 212);
    padding-top: 100px;
    text-shadow: 2px 2px 0px #343;
  }
  .form {
    padding: 20px;
    padding-top: 30px;
  }
</style>